<template>
  <el-main>
    <el-row>
      <el-col :span="22" :offset="1">
        <!-- <el-carousel height="350px" trigger="click" @change="getCarousel">
          <el-carousel-item v-for="(item,cur) in carouselArr" :key="cur">
            <img :src="item.url" alt="" />
          </el-carousel-item>
        </el-carousel> -->
        <div class="marvel-main">
          <!-- <header>
            <h2></h2>
            <span class="author"><br />
              </span>
          </header>
          <div class="marvel-content">
            <p class="justify">
              12Tony
              StarkTony21StarkTonyPepper
               Nick
              Fury
            </p>
            <img src="../assets/iron1.jpg" alt="">
          </div> -->
          <div :class="className" :style="{height:height,width:width}" ref="myEchart"></div>
          <div class="marvel-timeline">
            <time-line></time-line>
            <div class="marvel-rate">
              <div class="rate-title">
                <span class="span"><span class="iconfont iconmysql"></span></span>
                <el-rate ref="rate" @change="changeRate" v-model="rateVal" allow-half show-score :colors="['#99A9BF', '#F7BA2A', '#FF9900']"></el-rate>
              </div>
            </div>
          </div>

        </div>
      </el-col>
    </el-row>
  </el-main>
</template>

<script>
  import echarts from 'echarts'
  import timeLine from '../components/timeline'
  import {
    mapMutations,
    mapGetters
  } from 'vuex'
  export default {
    name: 'mainer',
    components: {
      timeLine
    },
    data() {
      return {
        className: 'chart',
        chart: null,
        height: '350px',
        width: '100%',
        rateVal: 0,
        carouselArr: [{
            url: require('../assets/imgs/swipers/carousel1.jpg')
          },
          {
            url: require('../assets/imgs/swipers/carousel2.jpg')
          },
          {
            url: require('../assets/imgs/swipers/carousel3.jpg')
          },
        ]
      }
    },
    created() {
      this.rateVal = this.$store.getters.getRate;
    },
    methods: {
      getCarousel(e) {
        // console.log(e);
      },
      changeRate(rate) {
        //bug  tab
        this.rateVal = rate;
        this.$store.commit('setRate', rate);
        this.$message({
          type: "success",
          message: ''
        })
      },
      initChart() {
        this.chart = echarts.init(this.$refs.myEchart, 'light');
        this.chart.showLoading();
        setTimeout(_ => {
          this.chart.hideLoading();
        }, 1000)
        this.chart.setOption({
          title: {
            text: ''
          },
          color: ['#ca8622', '#c23531', '#2f4554', '#61a0a8', '#d48265', '#91c7ae', '#749f83', '#bda29a', '#6e7074',
            '#546570', '#c4ccd3'
          ],
          /*hover   */
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          //
          toolbox: {
            feature: {
              dataView: {
                show: true,
                readOnly: false
              },
              magicType: {
                show: true,
                type: ['line', 'bar']
              },
              restore: {
                show: true
              },
              saveAsImage: {
                show: true
              }
            }
          },
          legend: {
            type: 'scroll',
            // selected: data.selected
            // data: ['', '', '', '', '', '']
            // orient:'vertical',
            // data: [{
            //   name: '',
            //   // 
            //   icon: 'circle',
            //   // 
            //   textStyle: {
            //     color: 'red'
            //   }
            // }]
          },
          xAxis: {
            data: ['', '', '', '', '', '']
          },
          yAxis: {},

          series: [{
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20],
            // label: {
            //   show: true,
            //   // 
            //   formatter: ''
            // },
            // // 
            // emphasis: {
            //   itemStyle: {
            //     // 
            //     color: '#61a0a8'
            //   },
            //   label: {
            //     show: true,
            //     // 
            //     formatter: 'hoverW'
            //   }
            // }
          }, {
            name: '',
            type: 'bar',
            data: [15, 25, 40, 20, 30, 40],
          }]
        });
      }
    },
    mounted() {
      this.initChart()
    },
    beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose()
      this.chart = null
    },
    // beforeRouteLeave(to, from, next) {
    //   console.log(to)
    // }
  }

</script>

<style lang="scss">
  .el-main {
    margin: 0;
    padding: 0;

    img {
      width: 100%;
      height: 100%;
      // z-index: 9999;
    }
  }

  .marvel-main {
    header {
      text-align: center;
    }

    /**/
    .chart {
      background: #f2f3f5;
      border-radius: 10px;
      box-shadow: 1px 1px 3p 2px #b7b8bb;
    }



    /**/
    .marvel-timeline {
      width: 100%;
    }

    /**/
    .marvel-rate {
      margin: 20px 0;
      width: 100%;
      display: flex;

      .rate-title {
        width: 100%;
        display: flex;
        justify-content: flex-end;
      }

      .span {
        margin-right: 20px;
      }
      .el-rate{
        width:165px;
      }
    }

    .author {
      display: block;
      width: 580px;
      height: 180px;
      background: #c0c4cc;
      margin: 0 auto;
      text-align: left;
      // text-indent: 18px;
      border-left: 8px solid #606266;
      padding-left: 20px;
    }

    .author::after {}
  }

</style>



