<template>
  <timeline timeline-theme="#1989fa" timeline-bg="#1989fa">
    <timeline-title font-color="#1989fa">
      <el-card>
        <p>MCU</p>
      </el-card>
    </timeline-title>
    <timeline-item bg-color="#9dd8e0" v-for="(item,index) in timelineData" :key="index">
      <div class="timeline-content">
        <el-tag hit>{{item.time}}{{item.name}}</el-tag>
        <el-card>
          <p>{{item.content}}</p>
        </el-card>
      </div>
    </timeline-item>
  </timeline>
</template>

<script>
  import {
    Timeline,
    TimelineItem,
    TimelineTitle
  } from 'vue-cute-timeline'
  export default {
    components: {
      Timeline,
      TimelineItem,
      TimelineTitle
    },
    data() {
      return {
        timelineData: [{
            time: '2008',
            name: '',
            content: '2008'
          },
          {
            time: '2008',
            name: '',
            content: '2003'
          },
          {
            time: '2010',
            name: '2',
            content: ''
          },
          {
            time: '2011',
            name: '',
            content: '2'
          },
          {
            time: '2011',
            name: '',
            content: ''
          },
          {
            time: '2012',
            name: '',
            content: ''
          },
          {
            time: '2013',
            name: '3',
            content: '-'
          },
          {
            time: '2013',
            name: '2',
            content: ''
          },
          {
            time: '2014',
            name: '2',
            content: ''
          },
          {
            time: '2014',
            name: '',
            content: ''
          },
          {
            time: '2015',
            name: '2',
            content: ''
          },
          {
            time: '2015',
            name: '',
            content: '2'
          },
          {
            time: '2016',
            name: '3',
            content: '2.540'
          },
          {
            time: '2016',
            name: '',
            content: '3'
          },
          {
            time: '2017',
            name: '2',
            content: ''
          },
          {
            time: '2017',
            name: '',
            content: ''
          },
          {
            time: '2017',
            name: '3',
            content: '3'
          },
          {
            time: '2018',
            name: '',
            content: ''
          },
          {
            time: '2018',
            name: '3',
            content: '3'
          },
          {
            time: '2018',
            name: '2',
            content: '2'
          },
          {
            time: '2019',
            name: '',
            content: ''
          },
        ],
      }
    }
  }

</script>

<style lang="scss">
  /**/
  .icon-heart {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  .timeline-title {}

    .timeline-content {
      .el-tag {
        margin-bottom: 10px;
        cursor: pointer;
      }
    }
</style>



